import { Component, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';

import { MessageService } from 'primeng/api';

import { DetalharService } from 'src/app/services/hall/detalhar/detalhar.service';
import { RpgService } from 'src/app/services/rpg/rpg.service';
import { ImagemZoomedComponent } from '../../imagem-zoomed/imagem-zoomed.component';
import { PresentDescriptionComponent } from '../../utils/present-description/present-description.component';
import { NpcDetailComponent } from '../npc-detail/npc-detail.component';

@Component({
  selector: 'app-rpg-detail',
  templateUrl: './rpg-detail.component.html',
  styleUrls: ['./rpg-detail.component.scss'],
})
export class RpgDetailComponent implements OnInit {

  rpg: any;

  faccoesBoas: any[] = [];
  faccoesHostis: any[] = [];
  faccoesNeutras: any[] = [];

  npcsAliado: any[] = [];
  npcsInimigos: any[] = [];
  npcsNeutros: any[] = [];

  capitulos: any[] = [];
  missoes: any[] = [];
  selectedCapitulo: any;

  regras: any[] = [];

  mapas: any[] = [];

  loading = true;
  private abaAtual = 'info';

  constructor(
    private modal: ModalController,
    private modalInterno: ModalController,
    private detalharService: DetalharService,
    private rpgService: RpgService,
    private toast: MessageService,
  ) {

  }

  ngOnInit() {
    const id = this.detalharService.getId();

    this.rpgService.findById(id)
      .subscribe(res => {
        console.log(res);
        const { nome, imgURL, historia, descricao, tipoRPG, faccoesBoas, faccoesHostis, faccoesNeutras, capitulos, aliados, neutros, inimigos, sistemas, mapas } = res;

        this.rpg = {
          nome,
          imgURL,
          historia,
          descricao,
          tipoRPG,
        };

        this.faccoesBoas = faccoesBoas;
        this.faccoesHostis = faccoesHostis;
        this.faccoesNeutras = faccoesNeutras;

        this.capitulos = capitulos;

        this.npcsAliado = aliados;
        this.npcsInimigos = inimigos;
        this.npcsNeutros = neutros;

        this.regras = sistemas;

        this.mapas = mapas;

        this.loading = false;
      },
        error => {
          console.log(error);
          this.loading = false;
        });
  }


  close() {
    this.modal.dismiss(null, null, "rpg-detail");
  }

  mudaAba(event) {
    this.selectedCapitulo = undefined;
    console.log(this.mapas);
  }

  callToast(severity, summary, detail) {
    this.toast.clear();
    this.toast.add({
      severity: severity,
      summary: summary,
      detail: detail
    });
  }

  getRecompensas(recompensas: []) {
    const recompensa = recompensas.join(', ')
    return recompensa;
  }

  async exibeDescricao(message: string) {

    if (!message)
      message = 'Não preenchido';

    const modal = await this.modalInterno.create({
      component: PresentDescriptionComponent,
      componentProps: {
        message
      },
      backdropDismiss: true,
      id: 'detail'
    });

    return await modal.present();

  }

  async zoomedImage(image: string) {

    if (!image)
      image = 'assets/imgs/dice.png';

    const modal = await this.modalInterno.create({
      component: ImagemZoomedComponent,
      componentProps: {
        src: image
      },
      backdropDismiss: true,
      cssClass: 'my-custom-modal-css',
      id: 'detail'
    });

    return await modal.present();

  }

  setSelectedCapitulo(event) {
    const { value } = event.detail;

    this.selectedCapitulo = value;
    this.missoes = value.missoes;
  }

  async setSelectedNpc(npc) {
    const modal = await this.modalInterno.create({
      component: NpcDetailComponent,
      componentProps: {
        passedNpc: npc,
      },
      backdropDismiss: false,
      cssClass: 'my-custom-modal-css',
      id: 'npc-detail'
    });

    return await modal.present();
  }


}
