import { Component, Input, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { isObject } from 'util';
import { MessageService } from 'primeng/api';
import { PaginacaoService } from 'src/app/services/utils/paginacao.service';

@Component({
  selector: 'app-imagem-zoomed',
  templateUrl: './imagem-zoomed.component.html',
  styleUrls: ['./imagem-zoomed.component.scss'],
})
export class ImagemZoomedComponent implements OnInit {

  @Input()
  src: string = '';
  defaultImg: string = 'assets/imgs/dice.png';

  isZoomed = false;

  constructor(public modalCtrl: ModalController,
    private service: PaginacaoService,
    private toast: MessageService) {
    this.src = this.src || this.defaultImg;
  }

  ngOnInit() {
    const p = this.service.getPromisse();
    if (p && (this.src == null || this.src.length < 5 || !this.src || isObject(this.src))) {
      this.src = p;
    }
    this.getImgZoomed();
  }

  close() {
    this.modalCtrl.dismiss(this.src);
  }

  getImg() {
    //console.log(isObject(this.src));
    if (this.src == null || this.src.length < 5 || !this.src || isObject(this.src)) {
      return this.defaultImg;
    } else {
      return this.src;
    }
  }

  getImgZoomed() {
    console.log(this.src);
    console.log(this.showFlag);
    if (this.src == null || this.src.length < 5 || !this.src || isObject(this.src)) {
      return [{ image: this.defaultImg }];
    } else {
      return [{ image: this.src }];
    }
  }

  update() {
    const p = this.service.getPromisse();
    p.subscribe(res => console.log(res));
  }

  zoom() { 
    this.isZoomed = !this.isZoomed;
  }

  showFlag: boolean = true;
  selectedImageIndex: number = 0;
  currentIndex = 0;

  showLightbox(index) {
    this.selectedImageIndex = index;
    this.showFlag = true;
  }

  closeEventHandler() {
    this.close();
  }

}
